
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import OFooter from "@/components/common/OFooter"
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
Group,
Cell,
XInput,
PopupPicker,
Datetime,
XTextarea,
XButton,
ViewBox,
AlertModule,
OFooter
},
data() {
return {
pageData: {},//
data: {},//
eventDescription: '',//
bizStatType: [],//
dictionary: [],//
disabledButton: false,
number: function (value) {
return {
valid: !value.replace(/[`A-Za-z0-9]/g, ''),
msg: ''
}
}

}
},
methods: {

replaceValue(value) {
this.eventDescription = value.replace(//g, '').replace(/\u2006/g, '');
},
getAlertDetail() {
let ruleName = ""
if (this.pageData.typeValue == '1') ruleName = "GetPostAlertDetail"
else ruleName = "GetPreAlertDetail"

let params = {
params: {
RuleName: ruleName,
Parm: { TimePoint: formatDate(new Date(), 'yyyyMMdd'), AlertNo: this.pageData.alertNo }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.data = data.RspMsg.result.ListItems.ListItem[0]
}
else {
this.showModule("", data.RspMsg.msg);
}
this.$store.commit('updateLoadingStatus', { isLoading: false })
};
callPuExctQryToOICS(params, callBackSuccess);
},//
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
},
showToast(text, width) {
this.$vux.toast.show({
text: text,
type: 'cancel',
width: width
})
},//
handleAlert() {
if (this.bizStatType.length <= 0) { this.showToast('', '8.5em'); return }
if (this.data.BranchNo == '') { this.showToast('', '6.5em'); return }
if (this.data.BranchNo.replace(/[`A-Za-z0-9]/g, '')) { this.showToast('', '16em'); return }
if (this.data.BranchNo.length != 4) { this.showToast('4', '10em'); return }
if (this.data.OrderFeedbackDate == '') { this.showToast('', '12em'); return }
if (this.eventDescription == '') { this.showToast('', '10em'); return }

this.$vux.loading.show({ text: '' })
this.disabledButton = true
const _this = this
let ruleName = ""
if (this.pageData.typeValue == '1') ruleName = "HandlePostAlert"
else ruleName = "HandlePreAlert"
let params = {
params: {
RuleName: ruleName,
Parm: {
TimePoint: formatDate(new Date(), 'yyyyMMdd')
, AlertNo: this.pageData.alertNo
, BranchNo: this.data.BranchNo
, OrderFeedbackDate: this.data.OrderFeedbackDate
, RuleType: this.data.RuleType
, AlertRisk: this.data.AlertRisk
, BizStatType: this.bizStatType[0]
, EventDescription: this.eventDescription
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {

if (data.RspMsg.result[0].DataFlag == '01') {
this.$vux.toast.show({
text: '',
width: '10em',
onHide() {
_this.$router.go(-1)
}
})
}
else {
this.showModule("", data.RspMsg.msg);
this.disabledButton = false
}
}
else {
this.showModule("", data.RspMsg.msg);
this.disabledButton = false
}

this.$vux.loading.hide()
};
callPuExctQryToOICS(params, callBackSuccess);
},//
onChangeDateValue(value) {
this.data.OrderFeedbackDate = value.replace(/-/g, '');
if (typeof this.$refs.datetime != 'undefined') {
this.$refs.datetime.currentValue = this.data.OrderFeedbackDate
}
}
},
mounted() {
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.dictionary = []
vm.data = {}
vm.eventDescription = ''
vm.bizStatType = []
vm.disabledButton = false
if (vm.$store.state.vux.dictionary.length > 0) vm.dictionary.push(vm.$store.state.vux.dictionary);
else vm.showModule("", "")
vm.pageData.alertNo = vm.$route.query.alertNo;
vm.pageData.typeValue = vm.$route.query.typeValue;
vm.pageData.titleName = vm.$route.query.titleName;
vm.getAlertDetail()
})
}
}



